$blue: #92C7CF;
$babyblue: #AAD7D9;

.Button {
  display: inline-block;
  color: white;
  font-weight: bold;
  outline: none;
  border-radius: 4px;
  border: none;
  cursor: pointer;

  &.large{
    height: 3rem;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 1.25rem;
  }

  background: $blue;
  &:hover {
    background: lighten($blue, 10%);
  }

  &:active{
    background: darken($blue, 10%);
  }

  & + & {
    margin-left: 1rem;
  }
}
